<script>
export default {
  name: "App",
  data() {
    return {
      deferredPrompt: null,
      dialog: false,
      about: false,
      info: false,
      hideTitle: true,
      isDownloadPage: true,
      isInstalled: false,
      loader: false,
      animateImg: false,
      isTimer: false,
      isDisabled: true,
      isDisabledBtn: true,
      isPwa: false,


      time: 0,
      isRunning: false,
      interval: null,
      installed: '',
  
      colors: [
        "indigo",
        "warning",
        "pink darken-2",
        "red lighten-1",
        "deep-purple accent-4",
        "indigo",
        "pink darken-2",

      ],
      slides: [
        "https://i.ibb.co/qWnNT40/screen1.jpg",
        "https://i.ibb.co/yhXhG3Q/screen2.jpg",
        "https://i.ibb.co/tL0p0cr/screen3.jpg",
        "https://i.ibb.co/NScZxNz/screen4.jpg",
        "https://i.ibb.co/d2PJ2zT/screen5.jpg",
        "https://i.ibb.co/p26bp3D/screen6.jpg",
        "https://i.ibb.co/qWnNT40/screen1.jpg"
        

        
      ],
    };
  },

  methods: {
    openApp() {
      console.log('opened')
      this.about = true;
      this.isInstalled = true;
    },
    
    toggleTimer() {
    
      if (this.isRunning) {
        clearInterval(this.interval);
    
      } else {
        this.interval = setInterval(this.incrementTime, 10);
      }
      this.isRunning = !this.isRunning
    },
    incrementTime() {
      if (this.time < 100) {
        this.time = parseInt(this.time) + 1;
        this.isDisabledBtn = true;
       
        
      } else {
        this.animateImg = false;
        this.loader = false;
        this.isTimer = false;
        this.isDisabled = false;
        this.isDisabledBtn = false;
         localStorage.setItem('installed', 'installed');
         
        
      }
    },

    showInfo() {
      this.info = true;
      this.about = false;
    },
    closeInfo() {
      this.info = false;
      this.about = true;
    },
    closeTitle() {
      this.hideTitle = false;
    },
    ckickInstallBtn() {
        this.deferredPrompt.prompt();
    //  this.isInstalled = true;
     // this.about = true;
     this.animateImg = true;
     this.loader = true;
     this.toggleTimer()
     this.isTimer = true;
     this.isDisabled = false;
     
     

    }
  },
  mounted() {
    if (localStorage.installed) {

      this.installed = localStorage.installed;
  
    } else { 
      return
    }

    if (localStorage.installed === 'installed') {
      this.isPwa = true;
    }

    console.log('test-version');
  },
  created() {
    
    window.addEventListener('beforeinstallprompt', (e) => {
  // Prevent Chrome 67 and earlier from automatically showing the prompt
    e.preventDefault();
  // Stash the event so it can be triggered later.
    this.deferredPrompt = e;
  // Update UI to notify the user they can add to home screen

  });
  }
};
</script>

